import React from "react";
import {useTranslation} from "react-i18next";
import {Modal, Accordion, Button, Card} from "react-bootstrap";

const MyModal = React.memo(({show, data, onClose, onAdd}) => {
    const {t} = useTranslation();

    return (
        <Modal.Dialog size="md" centered={true}>
            <Modal.Header closeButton={true}>
                <Modal.Title>{t("calendar.hourly_tracking")}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Accordion defaultActiveKey={0}>
                    {data?.map((item, index) => (
                        <Card key={`accordion-${index}`}>
                            <Card.Header>
                                <Accordion.Toggle as={Button} variant="link" eventKey={index}>{t('calendar.order')} #{item.order_id}</Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey={index}>
                                <Card.Body>
                                    <div className={'d-flex flex-row justify-content-between px-3'}>
                                        <div>{t('calendar.order')}</div>
                                        <div>#{item.order_id}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3'}>
                                        <div>{t('calendar.cost')}</div>
                                        <div>{item.price}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3'}>
                                        <div>{t('calendar.popup_count')}</div>
                                        <div>{item.count}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3'}>
                                        <div>{t('calendar.popup_status')}</div>
                                        <div>{item.is_payed ? t('calendar.is_paid') : t('calendar.is_not_paid')}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3'}>
                                        <div>{t('calendar.amount')}</div>
                                        <div>{item.credit ?? item.debit}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3'}>
                                        <div>{t('calendar.start_time')}</div>
                                        <div>{item.start_time?.split(' ')[1] ?? t('not-set')}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3'}>
                                        <div>{t('calendar.stop_time')}</div>
                                        <div>{item.stop_time?.split(' ')[1] ?? t('not-set')}</div>
                                    </div>
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    ))}
                </Accordion>
            </Modal.Body>
            <Modal.Footer>
                <div className={'d-flex flex-row justify-content-between w-100'}>
                    <div>
                        <Button variant="danger" onClick={onClose}>
                            {t("calendar.cancel")}
                        </Button>
                    </div>
                    <div>
                        <Button value={'primary'} onClick={() => {
                            onAdd(true)
                        }}>{t('calendar.add')}</Button>
                    </div>
                </div>
            </Modal.Footer>
        </Modal.Dialog>
    );
});

export {MyModal}